@charset "UTF-8";
.block-testimonials {
  width: 100%;
  display: flex;
}
.block-testimonials__inner {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
@media (min-width: 1024px) {
  .block-testimonials__inner {
    flex-direction: row;
  }
}
.block-testimonials__inner .col {
  width: 100%;
  min-height: 432px;
  position: relative;
  display: flex;
}
@media (min-width: 1024px) {
  .block-testimonials__inner .col {
    width: 50%;
    height: 100%;
    flex: 0 0 50%;
  }
}
.block-testimonials__inner .col__inner {
  padding: 80px 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 1024px) {
  .block-testimonials__inner .col__inner {
    height: 100%;
    padding: 120px 50px;
  }
}
@media (min-width: 1441px) {
  .block-testimonials__inner .col__inner {
    padding: 100px 50px;
  }
}
@media (min-width: 1441px) {
  .block-testimonials__inner .col:first-child {
    padding: 10px 0;
  }
}
.block-testimonials__inner .col:first-child .bg-img {
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  display: block;
  object-fit: cover;
  object-position: center center;
}
.block-testimonials__inner .col:first-child .col__inner {
  width: 100%;
  min-height: 100%;
  position: relative;
  z-index: 1;
  background-color: rgba(var(--secondary-color), 0.8);
}
@media (min-width: 1441px) {
  .block-testimonials__inner .col:first-child .col__inner {
    padding-left: 35%;
  }
}
.block-testimonials__inner .col:first-child .col__inner .block-title {
  margin-bottom: 40px;
  color: #fff;
}
.block-testimonials__inner .col:first-child .col__inner .content {
  margin-bottom: 25px;
}
@media (min-width: 1024px) {
  .block-testimonials__inner .col:first-child .col__inner .content {
    margin-bottom: 45px;
  }
}
.block-testimonials__inner .col:first-child .col__inner .content * {
  color: #fff;
}
.block-testimonials__inner .col:first-child .col__inner .btn {
  width: fit-content;
  padding: 15px 30px;
  display: flex;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .block-testimonials__inner .col:first-child .col__inner .btn {
    font-size: 1.6rem;
  }
}
.block-testimonials__inner .col:last-child {
  background-color: rgba(var(--primary-color), 1);
}
.block-testimonials__inner .col:last-child .col__inner {
  width: 100%;
  min-height: 100%;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials {
  width: 100%;
  margin-bottom: 50px;
  padding: 0 10px;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial {
  display: flex;
  flex-direction: column;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial:hover .photo {
  border-color: rgba(var(--secondary-color));
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial:hover .name::after {
  opacity: 0.5;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial .photo {
  width: 90px;
  height: 90px;
  margin-bottom: 30px;
  display: block;
  border: 5px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  transition: 0.3s linear;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial .photo img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
  border-radius: 50%;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial .name {
  width: 100%;
  margin-bottom: 40px;
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  color: #fff;
  font-family: var(--poppins);
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial .name::after {
  content: "“";
  margin-bottom: -35px;
  font-family: georgia;
  font-size: 160px;
  font-weight: 500;
  color: #ffffff;
  opacity: 0.06;
  transition: 0.3s linear;
}
@media (min-width: 768px) {
  .block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial .name::after {
    margin-bottom: 0;
    margin-right: 50px;
  }
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial .content {
  margin-bottom: 20px;
  color: #bcbcbc;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial .stars {
  width: 100%;
  display: flex;
}
.block-testimonials__inner .col:last-child .col__inner .testimonials .testimonial .stars svg {
  width: 12px;
  height: 12px;
  fill: rgba(var(--secondary-color));
}
.block-testimonials__inner .col:last-child .col__inner .swiper-pagination {
  padding: 0 10px;
  position: static;
  text-align: left;
}
.block-testimonials__inner .col:last-child .col__inner .swiper-pagination .swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  margin: 0 5px;
  background-color: #bcbcbc;
  opacity: 1;
}
.block-testimonials__inner .col:last-child .col__inner .swiper-pagination .swiper-pagination-bullet-active {
  background-color: rgb(var(--secondary-color));
}